:root {
    --background: #eeeeeeff;
    --primary: #673ab7ff;
}

body {
    margin: 0;
    font-family: "Muli", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 1rem;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

a {
    text-decoration: none;
}

h2 {
    font-size: 2rem;
    margin: 0px 10px;
}

.morph{border-radius:50px;padding:40px 50px;background-color:var(--background);box-shadow:20px 20px 50px #cfcfcf,-20px -20px 50px #fff;border:1px solid #fff8}.morph.small{border-radius:25px;padding:20px 25px;box-shadow:12px 12px 25px #cfcfcf,-12px -12px 25px #fff}.morph.inset{box-shadow:inset 20px 20px 50px #cfcfcf,inset -20px -20px 50px #fff}.morph.btn{font-weight:700}.morph.btn:hover{box-shadow:8px 8px 20px #cfcfcf,-8px -8px 20px #fff}.morph.btn:active{box-shadow:inset 7px 7px 13px #58319c, inset -7px -7px 13px #7643d2;color:#e0e0e0;background-color:var(--primary)}

.App {
    background-color: var(--background);
    min-height: 100vh;
    max-height: 100vh;
    text-align: center;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: black;
    overflow-y: auto;
}

.App-logo-container {
    margin-bottom: 60px;
}

a {
    color: var(--primary);
    font-weight: 700;
}

.App-made-with-heart {
    position: absolute;
    bottom: 0;
    left: 0;
    color: #0008;
    font-size: 11px;
    padding: 5px 10px;
}

.App-made-with-heart__heart {
    color: var(--primary);
    font-size: 15px;
}

